.root {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #424242;
}

.top-bar-input {
  background-color: #232323;
  border: none;
  outline: none;
  padding: 10px;
  color: #BDBDBD;
  margin: 0 0; width: 100%;
  font-family: "Space Mono", monospace;
  font-size: 12.5pt;
  overflow-x: scroll;
}

.top-bar-input::-webkit-input-placeholder {
  color: #696969;
  opacity: 1;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.top-bar-input:-ms-input-placeholder {
  color: #696969;
  opacity: 1;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.top-bar-input::-ms-input-placeholder {
  color: #696969;
  opacity: 1;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.top-bar-input::placeholder {
  color: #696969;
  opacity: 1;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.top-bar-input:focus::-webkit-input-placeholder{
  opacity: 0;
}

.top-bar-input:focus:-ms-input-placeholder{
  opacity: 0;
}

.top-bar-input:focus::-ms-input-placeholder{
  opacity: 0;
}

.top-bar-input:focus::placeholder{
  opacity: 0;
}

.console {
  padding: 0;
  font-family: "Space Mono", monospace;
  font-size: 12pt;
  color: #BDBDBD;
}

.console ul {
  list-style: none;
  margin-left: 0;
  margin-top: 0.5em;
  padding-left: 0.5em;
}

.console li {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  -ms-flex-align: start;
      align-items: flex-start;
}

.console li.sys {
  color: #96C786;
}

.console li p.prefix {
  margin: 0 5pt;
  -ms-flex: none;
      flex: none;
}

.console li.usr p.prefix {
  color: #919191;
}

.console li.sys p.prefix {
  color: #59a469;
}

.console li p.message {
  margin: 0;
  -ms-flex: auto;
      flex: auto;
}


